.createTaskBlur {
    z-index: 1;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    position: absolute;
}

.modal_create {
    padding: 20px;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    margin: auto;
    max-height: 200px;
    width: 60vw;
    box-shadow: 1px 1px 20px #e30d32;
    background-color: rgba(143, 188, 143, 0.91);
}


.modal_create input {
    background-color: #939d90;
}

@media screen and (max-width: 704px) {
    .modal_create {
        width: 90vw;
    }
}